<script>
  import { createEventDispatcher } from "svelte";
  import { get } from "svelte/store";
  import { possibilitiesStore } from "../Stores/possibilities.js";

  const dispatch = createEventDispatcher();
  let possibilitiesfile;

  function closeModal() {
    dispatch("close");
  }
  function download() {
    const possibilities = get(possibilitiesStore);
    let arrays = [];
    for (let d of possibilities) {
      if (d.artifacts != "")
        possibilities["artifact"].push(d.artifacts.replace(/ /g, ""));
      if (d.attributes != "")
        possibilities["attribute"].push(d.attributes.replace(/ /g, ""));
      if (d.experiences != "")
        possibilities["experience"].push(d.experiences.replace(/ /g, ""));
      if (d.inspirations != "")
        possibilities["inspiration"].push(d.inspirations.replace(/ /g, ""));
      if (d.medium != "")
        possibilities["media"].push(d.medium.replace(/ /g, ""));
    }
    // console.log("downlo", possibilities);
    // var csv = Papa.unparse(yourData);
  }

  function upload() {
    let file = document.getElementById("variablescsv").files[0];
    var reader = new FileReader();
    reader.readAsText(file);
    reader.onload = function(event) {
      var csvData = event.target.result;
      let data = CSVToArray(csvData);
      if (data && data.length > 0) {
        closeModal();
        dispatch("data", {
          name: "data",
          value: csvData
        });
      } else {
        alert("No data to import!");
      }
    };
    reader.onerror = function() {
      alert("Unable to read " + file.fileName);
    };
    console.log(file);
    let csv = CSVToArray(file);
  }

  function CSVToArray(strData, strDelimiter) {
    // Check to see if the delimiter is defined. If not,
    // then default to comma.
    strDelimiter = strDelimiter || ",";

    // Create a regular expression to parse the CSV values.
    var objPattern = new RegExp(
      // Delimiters.
      "(\\" +
        strDelimiter +
        "|\\r?\\n|\\r|^)" +
        // Quoted fields.
        '(?:"([^"]*(?:""[^"]*)*)"|' +
        // Standard fields.
        '([^"\\' +
        strDelimiter +
        "\\r\\n]*))",
      "gi"
    );

    // Create an array to hold our data. Give the array
    // a default empty first row.
    var arrData = [[]];

    // Create an array to hold our individual pattern
    // matching groups.
    var arrMatches = null;

    // Keep looping over the regular expression matches
    // until we can no longer find a match.
    while ((arrMatches = objPattern.exec(strData))) {
      // Get the delimiter that was found.
      var strMatchedDelimiter = arrMatches[1];

      // Check to see if the given delimiter has a length
      // (is not the start of string) and if it matches
      // field delimiter. If id does not, then we know
      // that this delimiter is a row delimiter.
      if (strMatchedDelimiter.length && strMatchedDelimiter !== strDelimiter) {
        // Since we have reached a new row of data,
        // add an empty row to our data array.
        arrData.push([]);
      }

      var strMatchedValue;

      // Now that we have our delimiter out of the way,
      // let's check to see which kind of value we
      // captured (quoted or unquoted).
      if (arrMatches[2]) {
        // We found a quoted value. When we capture
        // this value, unescape any double quotes.
        strMatchedValue = arrMatches[2].replace(new RegExp('""', "g"), '"');
      } else {
        // We found a non-quoted value.
        strMatchedValue = arrMatches[3];
      }

      // Now that we have our value string, let's add
      // it to the data array.
      arrData[arrData.length - 1].push(strMatchedValue);
    }

    // Return the parsed data.
    return arrData;
  }
</script>

<div class="mzp-c-modal mzp-has-media" role="dialog">
  <div class="mzp-c-modal-window">

    <div class="mzp-c-modal-inner">

      <header>
        <h2>Upload</h2>
      </header>

      <div class="mzp-c-modal-close">
        <button
          type="button"
          on:click={closeModal}
          class="mzp-c-modal-button-close"
          title="Close modal">
          Close
        </button>
      </div>

      <div class="mzp-u-modal-content mzp-c-modal-overlay-contents">
        <label for="text">
          Please upload your variables
          <br />
          <input
            type="file"
            name="variablescsv"
            accept=".csv"
            id="variablescsv" />
        </label>
        <button
          on:click={upload}
          class="mzp-c-button mzp-t-secondary mzp-t-dark">
          Upload
        </button>

        <button
          on:click={closeModal}
          class="mzp-c-button mzp-t-secondary mzp-t-dark">
          Close
        </button>
      </div>
    </div>

    <div class="mzp-c-modal-inner">
      <header>
        <h2>Download</h2>
      </header>
      <div class="mzp-u-modal-content mzp-c-modal-overlay-contents">
        <label for="text">Download your variables as a file</label>
        <h5>Comming Soon!</h5>
        <!-- <button
          on:click={download}
          class="mzp-c-button mzp-t-secondary mzp-t-dark">
          Download
        </button> -->
      </div>

    </div>

  </div>

</div>
