<script>
  import Statement from "./Components/Statement.svelte";
  import UploadModal from "./Components/FileUpload.svelte";
  import { possibilitiesStore } from "./Stores/possibilities.js";
  import { createEventDispatcher } from "svelte";

  let showUploadeModal = false;

  let possibilities = {};

  function openUploadModal() {
    showUploadeModal = true;
  }

  function onData(event) {
    let { name, value } = event.detail;
    //0 artifact
    //1 inspiration
    //2 experience
    //3 attribute
    //4 media
    var results = Papa.parse(value, {
      delimiter: "", // auto-detect
      newline: "", // auto-detect
      header: true,
      delimitersToGuess: [",", "\t", "|", ";", Papa.RECORD_SEP, Papa.UNIT_SEP]
    });

    //
    possibilities["artifact"] = [];
    possibilities["inspiration"] = [];
    possibilities["experience"] = [];
    possibilities["attribute"] = [];
    possibilities["media"] = [];
    for (let d of results.data) {
      if (d.artifacts != "")
        possibilities["artifact"].push(d.artifacts.replace(/ /g, ""));
      if (d.attributes != "")
        possibilities["attribute"].push(d.attributes.replace(/ /g, ""));
      if (d.experiences != "")
        possibilities["experience"].push(d.experiences.replace(/ /g, ""));
      if (d.inspirations != "")
        possibilities["inspiration"].push(d.inspirations.replace(/ /g, ""));
      if (d.medium != "")
        possibilities["media"].push(d.medium.replace(/ /g, ""));
    }
    // console.log(possibilities);
    possibilitiesStore.set(possibilities);
    // for (let i = 0; i < value.length - 2; i++) {
    //   console.log(value[i]);
    // }
  }
</script>

<div class="mzp-l-content">
  <main class="mzp-l-main">
    <Statement />
    <button on:click={openUploadModal} class="mzp-c-button">Input</button>
  </main>
  

  {#if showUploadeModal}
    <UploadModal on:data={onData} on:close={() => (showUploadeModal = false)} />
  {/if}

</div>
