<script>
  import { createEventDispatcher } from "svelte";

  export let name = "dummy";
  export let value = "dummy";
  export let text = "dummy";
  export let color = "";

  const dispatch = createEventDispatcher();

  function selectValue() {
    dispatch("select", {
      name: name,
      value: value
    });
  }
</script>

<!-- <h5>  </h5> -->

<button id={name} on:click={selectValue} class="mzp-c-button mzp-t-secondary">
   {text}
</button>
<h2 class={name}> {value} </h2>
