<script>
  import VariableType from "./VariableType.svelte";
  import SelectValue from "./SelectValue.svelte";
  import { possibilitiesStore } from "../Stores/possibilities.js";

  // export let artifacts = ["a1", "a2", "a3"];
  // export let inspirations = ["i1", "i2", "i3"];
  // export let experiences = ["e1", "e2", "e3"];
  // export let attributes = ["at1", "at2", "at3"];
  // export let medias = ["m1", "m2", "m3"];

  export let possibilities = {};
  let selected_possibilities = Object.assign({}, possibilities);
  const unsubscribe = possibilitiesStore.subscribe(value => {
    possibilities = value;
    selected_possibilities = Object.assign({}, possibilities);
  });

  console.log(possibilities);
  // possibilities["artifact"] = ["a1", "a2", "a3"];
  // possibilities["inspiration"] = ["i1", "i2", "i3"];
  // possibilities["experience"] = ["e1", "e2", "e3"];
  // possibilities["attribute"] = ["at1", "at2", "at3"];
  // possibilities["media"] = ["m1", "m2", "m3"];
  let showSelectValueModal = false;
  let selectedVariable;

  function selectValue(event) {
    let { name, value } = event.detail;
    selectedVariable = name;
    // console.log("sel2", sel);
    showSelectValueModal = true;
    // clearInterval(interval);

    // console.log(name, value);
  }

  let variables = {};
  variables["artifact"] = "";
  variables["inspiration"] = "";
  variables["experience"] = "";
  variables["attribute"] = "";
  variables["media"] = "";

  let all = ["artifact", "inspiration", "experience", "attribute", "media"];
  let constants = [
    // "artifact",
    // "inspiration",
    // "experience",
    // "attribute",
    // "media"
  ];
  let selected_values = {};
  function selected(event) {
    let { name, value } = event.detail;

    if (value.length == 0) {
      // undoConstant(name);
      selected_values[name] = [];
      selectVariableValue(name, possibilities[name]);
    } else {
      // makeConstant(name);

      let _values = value.map(v => v.split("-")[2]);
      selected_values[name] = _values;
      selectVariableValue(name, _values);
    }
  }
  function makeConstant(variable) {
    // undoConstant(variable);
    constants.push(variable);
  }

  function undoConstant(variable) {
    constants = constants.filter(v => (v === variable ? false : true));
  }

  function selectVariableValue(variable, values) {
    // CHECK ("selected variable", variable, _values);
    selected_possibilities[variable] = values;
  }
  // undoConstant("media");
  // undoConstant("experience");
  // makeConstant("experience");

  function randomize(constant = constants) {
    let to_randomize = all.filter(v => (constants.includes(v) ? false : true));
    for (let v of to_randomize) {
      variables[v] =
        selected_possibilities[v][
          Math.floor(Math.random() * selected_possibilities[v].length)
        ];
    }
  }

  function addValue(event) {
    let { name, value } = event.detail;
    possibilities[name].unshift(value);
  }
  randomize();
</script>

<article>
  <section class="mzp-has-aspect-3-2 ">
    <VariableType
      on:select={selectValue}
      name="artifact"
      text="Design"
      value={variables['artifact']} />
  </section>
  <section class="mzp-has-aspect-3-2 ">
    <VariableType
      on:select={selectValue}
      name="inspiration"
      text="inspired by"
      value={variables['inspiration']} />
  </section>
  <section class="mzp-has-aspect-3-2 ">
    <VariableType
      on:select={selectValue}
      name="experience"
      text="that is"
      value={variables['experience']} />
  </section>
  <section class="mzp-has-aspect-3-2 ">
    <VariableType
      on:select={selectValue}
      name="attribute"
      text="through"
      value={variables['attribute']} />
  </section>
  <section class="mzp-has-aspect-3-2 ">
    <VariableType
      on:select={selectValue}
      name="media"
      text="using"
      value={variables['media']} />
  </section>
</article>
<button on:click={randomize} class="mzp-c-button">Randomize</button>

{#if showSelectValueModal}
  <SelectValue
    variable={selectedVariable}
    values={possibilities[selectedVariable]}
    selectedvalues={selected_values}
    on:newvalue={addValue}
    on:selected={selected}
    on:close={() => (showSelectValueModal = false)} />
{/if}

